.landing-main{
    width: 100%;
    height: 100vh;
    background-image: url(/static/media/landing.e677f14c.jpeg);
    background-size: cover;
    font-family: 'Cabin', sans-serif;
}

.landing-main .head1a{
    position: absolute;
    color: white;
    font-size: 50px;
    margin-top: 60vh;
    padding-right: 20px;
    padding-left: 20px;
    background-color: #230342;
}

.landing-main .head1b{
    position: absolute;
    color: white;
    font-size: 50px;
    margin-top: 72vh;
    padding-right: 20px;
    padding-left: 20px;
    background-color: #230342;
}

.landing-main .head2{
    position: absolute;
    color: white;
    font-size: 20px;
    margin-top: 84vh;
    padding-right: 20px;
    padding-left: 20px;
    /* background-color: #230342; */
}

.headbtn{
    margin-top: 90vh;
    margin-left: 20px;
    height: 50px;
    width: 120px;
    border: none;
