/* Tooltip*/
.tooltiptext {
    visibility: hidden;
    width: 150px;
    background-color: black;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
    position: absolute;
    z-index: 1;

  }
  
  .userform div:hover .tooltiptext {
    visibility: visible;
  }

/* 13 inch  */
@media screen and (max-width: 1300px) and (-webkit-min-device-pixel-ratio: 1){
    .userform div select{
        width: 260px;
    } 
}

